












































import Vue from 'vue';

export default Vue.extend({
    created() {
        this.getTargetSurvey();

    },
    props: ['id'],
    data() {
        return {
            survey: {},
            isEditing: false

        }
    },

    methods: {
        getTargetSurvey(): void {
            const url = `${this.ApiUrl}surveys`;
            this.$http.get(`${url}/${this.id}`).then(
                res => {
                    if (res.status !== 200) {
                        this.survey = {};
                        return;
                    }
                    this.survey = res.data;
                }
            );
        },
        toggleSurveyEditing(): void {
            this.isEditing = !this.isEditing;
        }
    }
})
    

